import { Component, OnInit } from '@angular/core';
import { AppCryptoService } from '../app-crypto.service';

@Component({
  selector: 'app-callback',
  templateUrl: './callback.component.html',
  styleUrls: ['./callback.component.sass']
})
export class CallbackComponent implements OnInit {

  constructor(private AppCrypto: AppCryptoService) { }

  ngOnInit() {
  	try {
      const state_token = this.parseToken(this.getStateToken());
      const id_token = this.parseToken(this.getIdToken());
      // const verified = this.verifyTokens(state_token, id_token);

      // console.log(state_token.REDIRECT_URL);
      console.log(state_token);
      console.log(id_token);
      // console.log(`verified: ${verified}`);

      this.redirect(state_token, id_token);
  	} catch (err) {
  		console.log(err.message);
  	}
  }

  private getStateToken(): string {
    const location = window.location.hash || window.location.search;
    const state = this.AppCrypto.getParameter(location, 'state');
    const base64Token = localStorage.getItem(state);

    return base64Token;
  }

  private getIdToken(): string {
    const location = window.location.hash || window.location.search;
    const base64_id_token = this.AppCrypto.getParameter(location, 'id_token');

    return base64_id_token;
  }

  private parseToken(base64Token: string): any {
    const tokenPayload = base64Token.split('.')[1];
    const tokenString = this.AppCrypto.decode(tokenPayload);
    const token = JSON.parse(tokenString);

    return token;
  }

  private verifyTokens(state_token: any, id_token: any): boolean {
    const nonce = state_token.nonce === id_token.nonce;
    const aud = state_token.aud === id_token.aud;
    const time = Math.floor(Date.now() / 1000);
    const exp = state_token.exp && id_token.exp > time;

    console.log(`time: ${time}`);
    console.log(`nonce: ${nonce}`);
    console.log(`aud: ${aud}`);
    console.log(`exp: ${exp}`);

    return ( nonce && aud && exp );
  }

  private redirect(state_token: any, id_token: any): void {
    if (this.verifyTokens(state_token, id_token)) {
      console.log(`verified: ${true}`);
      console.log(state_token.REDIRECT_URL);
      // console.log(state_token);
      // console.log(id_token);
    } else {
      console.log(`verified: ${false}`);
    }
  }
}
