import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Base64 } from 'js-base64';
import { Observable } from 'rxjs';
import RandomBytes from 'randombytes';
import SHA from 'sha.js';

@Injectable({
  providedIn: 'root'
})
export class AppCryptoService {
	constructor(private http: HttpClient) { }

	public encode(message): string {
		return Base64.encodeURI(message);
	}

	public decode(message): string {
		return Base64.decode(message);
	}

	public sha256(message): string {
		return SHA('sha256').update(message).digest('hex');
	}

	public randomBytes(bytes=32): string {
		return RandomBytes(bytes);
	}

	public random(bytes=32): string {
		return Base64.encodeURI(RandomBytes(bytes));
	}

	public codeChallenge(randomData=null): string {
		const data = randomData ? randomData : RandomBytes(32);

		return Base64.encodeURI(SHA('sha256').update(data).digest('hex'));
	}

	public generateUrl(uri: string): Observable<any> {
		return this.http.get(uri);
	}

	public getParameter(parameters: string, key: string) {
		const params = parameters[0] === '?' || '#' ? parameters.substring(1) : parameters;
		const paramsObj = new URLSearchParams(params);

		return paramsObj.get(key);
	}
}
